import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-find-job',
  templateUrl: './find-job.component.html',
  styleUrls: ['./find-job.component.css']
})
export class FindJobComponent implements OnInit {
  keywordsIsFocused: boolean;
  locationIsFocused: boolean;
  selectedCity: any;
  cities = [
    {id: 1, name: 'New York'},
    {id: 2, name: 'Missouri'},
    {id: 3, name: 'Los Angeles'}
  ];

  constructor() { }

  ngOnInit() {
  }

  focusFunction(key) {
    this[`${key}IsFocused`] = true;
  }

  focusOutFunction(key) {
    this[`${key}IsFocused`] = false;
  }

}
