import {Component, OnInit, AfterViewInit} from '@angular/core';

declare var require: any;

const Sticky = require('sticky-js');

@Component({
  selector: 'app-header',
  templateUrl: 'header.component.html',
  styleUrls: ['./header.component.css']
})

export class HeaderComponent implements AfterViewInit, OnInit {
  focusedFirst = '';
  focusedSecond = '';
  focusedThird = '';
  focusedForth = '';

  constructor() {}

  changeStyle($event, key) {
    this[`focused${key}`] = $event.type === 'mouseover' ? 'focus' : '';
    this.alignChildUl($event);
  }

  ngOnInit(): void {
    window.onload = () => {
      document.getElementById('page-loader').classList.add('loaded');
    };
  }

  private alignChildUl($event) {
    const li = $event.target.closest('li');
    const childUl = li.children.item(1);
    if (!childUl) { return; }
    // if ($event.type !== 'mouseover') {
    //   childUl.style.left = '0px';
    //   return;
    // }
    const childUlPosition = childUl.getBoundingClientRect();
    const outOfWindow = document.documentElement.clientWidth - (childUlPosition.x + childUlPosition.width);
    if (outOfWindow < 0) {
      childUl.style.left = (outOfWindow - 30 ) + 'px';
    }
  }

  ngAfterViewInit(): void {
    const sticky = new Sticky('.rd-navbar');
  }
}
