import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {HomeComponent} from './components/home/home.component';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { LocationsComponent } from './components/locations/locations.component';
import { OurCoreExpertiseComponent } from './components/our-core-expertise/our-core-expertise.component';
import { MissionAndHistoryComponent } from './components/mission-and-history/mission-and-history.component';
import { NewsAndEventsComponent } from './components/news-and-events/news-and-events.component';
import { WorkAtHireyaComponent } from './components/work-at-hireya/work-at-hireya.component';
import { ForApplicantsComponent } from './components/for-applicants/for-applicants.component';
import { ForCompaniesComponent } from './components/for-companies/for-companies.component';
import { RecruitmentAndEvaluationOfStaffComponent } from './components/recruitment-and-evaluation-of-staff/recruitment-and-evaluation-of-staff.component';
import { TemporaryEmploymentAndLeasingComponent } from './components/temporary-employment-and-leasing/temporary-employment-and-leasing.component';
import { ExecutiveRecruitmentComponent } from './components/executive-recruitment/executive-recruitment.component';
import { OutsourcingComponent } from './components/outsourcing/outsourcing.component';
import { ConsultingComponent } from './components/consulting/consulting.component';
import { TermsOfUseComponent } from './components/terms-of-use/terms-of-use.component';
import { PrivacyPolicyComponent } from './components/privacy-policy/privacy-policy.component';
import { ApplicantFormComponent } from './components/applicant-form/applicant-form.component';
import { BlogComponent } from './components/blog/blog.component';
import { ContactUsComponent } from './components/contact-us/contact-us.component';
import { FaqComponent } from './components/faq/faq.component';
import { FindJobComponent } from './components/find-job/find-job.component';
import { Home2Component } from './components/home2/home2.component';
import { PostJobComponent } from './components/post-job/post-job.component';
import { ProjectManagersComponent } from './components/project-managers/project-managers.component';
import { RegisterLoginComponent } from './components/register-login/register-login.component';
import { ResumePageComponent } from './components/resume-page/resume-page.component';
import { YourCareerStartsHereComponent } from './components/your-career-starts-here/your-career-starts-here.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent},
  { path: 'about-us', component: AboutUsComponent, },
  { path: 'locations', component: LocationsComponent, },
  { path: 'our-core-expertise', component: OurCoreExpertiseComponent, },
  { path: 'mission-and-history', component: MissionAndHistoryComponent, },
  { path: 'news-and-events', component: NewsAndEventsComponent, },
  { path: 'work-at-hireya', component: WorkAtHireyaComponent, },
  { path: 'for-applicants', component: ForApplicantsComponent, },
  { path: 'for-companies', component: ForCompaniesComponent, },
  { path: 'recruitment-and-evaluation-of-staff', component: RecruitmentAndEvaluationOfStaffComponent, },
  { path: 'temporary-employment-and-leasing', component: TemporaryEmploymentAndLeasingComponent, },
  { path: 'outsourcing', component: OutsourcingComponent, },
  { path: 'executive-recruitment', component: ExecutiveRecruitmentComponent, },
  { path: 'consulting', component: ConsultingComponent, },
  { path: 'terms-of-use', component: TermsOfUseComponent, },
  { path: 'privacy-policy', component: PrivacyPolicyComponent, },
  { path: 'applicants-form', component: ApplicantFormComponent, },
  { path: 'blog', component: BlogComponent},
  { path: 'contact-us', component: ContactUsComponent},
  { path: 'faq', component: FaqComponent},
  { path: 'find-job', component: FindJobComponent},
  { path: 'home2', component: Home2Component},
  { path: 'post-job', component: PostJobComponent},
  { path: 'project-managers', component: ProjectManagersComponent},
  { path: 'register-login/:tab', component: RegisterLoginComponent},
  { path: 'resume-page', component: ResumePageComponent},
  { path: 'your-career-starts-here', component: YourCareerStartsHereComponent},
  { path: '**', component: HomeComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
