import {AfterContentInit, Component, ViewEncapsulation} from '@angular/core';
import {jarallax, jarallaxElement} from 'jarallax';

@Component({
  selector: 'app-rd-parallax',
  templateUrl: './rd-parallax.component.html',
  styleUrls: ['./rd-parallax.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class RdParallaxComponent implements AfterContentInit {
  cities = [
    {id: 1, name: 'New York'},
    {id: 2, name: 'Missouri'},
    {id: 3, name: 'Los Angeles'}
  ];

  selectedCity: any;

  inputIsFocused: boolean;

  constructor() {}

  ngAfterContentInit() {
    jarallaxElement();
    jarallax(document.querySelectorAll('.jarallax'), {
      speed: 0.2
    });
  }

  focusFunction() {
    this.inputIsFocused = true;
  }

  focusOutFunction() {
    this.inputIsFocused = false;
  }
}
